.title-cont {
  height: 100%;
  position: absolute;
}

@media only screen and (min-width: 651px) {
  .title-cont {
    transform: translate(65%, 50%);
    width: 40%;
  }
}

@media only screen and (max-width: 650px) {
  .title-cont {
    transform: translate(10%, 68%);
    width: 80%;
  }
}
